body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
/* gradient body bkg */
  background: hsla(232, 73%, 65%, 1);

  background: linear-gradient(90deg, hsla(232, 73%, 65%, 1) 0%, hsla(261, 72%, 65%, 1) 19%, hsla(266, 83%, 23%, 1) 71%, hsla(266, 83%, 23%, 1) 100%);

  background: -moz-linear-gradient(90deg, hsla(232, 73%, 65%, 1) 0%, hsla(261, 72%, 65%, 1) 19%, hsla(266, 83%, 23%, 1) 71%, hsla(266, 83%, 23%, 1) 100%);

  background: -webkit-linear-gradient(90deg, hsla(232, 73%, 65%, 1) 0%, hsla(261, 72%, 65%, 1) 19%, hsla(266, 83%, 23%, 1) 71%, hsla(266, 83%, 23%, 1) 100%);

  filter: progid: DXImageTransform.Microsoft.gradient(startColorstr="#6576e7", endColorstr="#9265e6", GradientType=1);


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.link {
  cursor: pointer;
}

/* Add shadows to project thumbnails */
#my-projects > div.MuiBox-root.css-1dlcyqs > div{
  background-color: #e4ebfd;
  filter: drop-shadow(2px 4px 6px black);
}

/*** Home Hero Tagline ***/
#foreground > h1 {
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-weight: 600;
  font-size: 4rem;
   letter-spacing: 0em;
 
}

@media only screen and (max-width: 768px) {
  #foreground > h1 {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 480px) {
  #foreground > h1 {
    font-size: 1rem;
  }
}

/* change the color for contact form outline */

.MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline {
  border-color: #97FFF4;
  color: #97FFF4;
}

#about > form > div > label{
  color: #97FFF4;
}


/* contact form button styling */


/***** SPRING REACT ANIMATION *****/

.animation_layer {
  min-height: 100vh;
  background-position: bottom center;
  background-size: repeat-x;
  width: 100%;
  position: absolute;
}



.parallax::-webkit-scrollbar {
  display: none;
}

#imgBackgroundBase {
  background-image: url('./media/01_parallax_bkg_base.png');
}

#imgBackgroundGlitter {
  background-image: url('./media/02_parallax_bkg_glitter.png');
}

#imgThirdRow {
  background-image: url('./media/03_parallax_3rd_row.png');
}

#imgSecondRow {
  background-image: url('./media/04_parallax_2nd_row.png');
}

#imgFirstRow {
  background-image: url('./media/05_parallax_1st_row.png');
}